#process .wrap{
  max-width: 460px;
}
.wrap:after { clear: both }

.process-flow>li h4{
  font-family: "Poppins", sans-serif !important;
  font-size: 1.8em;
  font-weight: 600;
  text-align: right;
  color: #18f7f9;
  width: 137px;
  padding: 0 37px 0 0;
  display: inline-block;
  position: relative;
  top: -26px;
  right: 50px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
}




#process{
  padding: 5% 0;
  /* background-color: rgba(0,0,0,.6); */
  clear: both;
}
#process p:nth-child(1){
  font-family: "Poppins", sans-serif !important;
  font-size: 2em;
  font-weight: 900;
  color: #18f7f9;
  margin: 4% 0 8% 0;
  display: block;
  padding-left: 4.5%;
}


#process ol.points{
  font-family: "Poppins", sans-serif !important;
  list-style-type: disc;
  display: inline-block;
  margin: 0 0 0 39px;
  position: relative;
  top: 3px;
  font-size: 1.4em;
  width: 250px;
}

.points li{
  padding-bottom: 7px;
  font-family: "Poppins", sans-serif !important;
}
 .points li:nth-last-child(1){
  padding-bottom: 0;
 }

.process-flow>li {color:#4c4c4e;-webkit-transition:opacity 1s ease-out;-moz-transition:opacity .5s ease-out;transition:opacity .5s ease-out;-webkit-opacity:.5;-moz-opacity:.5;opacity:.5 ;width: 600px;list-style: none;}
.process-flow>li.active {   list-style-type: none;color:#fff;-webkit-opacity: 1;-moz-opacity:1;opacity:1 ; margin: 0; padding: 0;-webkit-transition:opacity 1s ease-out;-moz-transition:opacity 1s ease-out;transition:opacity 1s ease-out;-webkit-opacity:1;-moz-opacity:1;opacity:1  }
.process-flow>li p { margin-bottom: 0 ; display: inline;}
.process-gem {width:73px;height:73px;-webkit-border-radius:3px;-moz-border-radius:3px;border-radius:3px;-webkit-transition:background .7s ease-out;-moz-transition:background .7s ease-out;transition:background .7s ease-out ;display: inline-block;}
.process-gem.special { width: 103px;height:103px }
.process-gem.special img {  }
.process-gem img {position:absolute; -webkit-opacity:1;-moz-opacity:1;opacity:1;-webkit-transition:opacity .7s ease-out;-moz-transition:opacity .7s ease-out;transition:opacity .7s ease-out ; border: 0 none;}
.process-gem img:last-child { -webkit-opacity: 0;-moz-opacity:0;opacity:0 ;}
.process-gem.active { background: none ; display: inline-block; box-shadow: 0 0 22px rgba(0,0,0,.3);}
.process-gem.active img:first-child {
	-webkit-opacity: 0;
	-moz-opacity:0;
	opacity:0;
	display: none;
	position: relative;
	transition: 0.5s all ease-in-out;
}
.process-gem.active img:last-child { -webkit-opacity: 1;-moz-opacity:1;opacity:1;}
.bar { background: #4c4c4e; height: 100px; width: 5px; margin: 0px 0 15px 213px;}
.bar.active .progress { height: 100px; width: 5px; color:#18f7f9; background:#18f7f9;}
.progress { background: #18f7f9;height:0;-webkit-transition:height 1s ease-out;-moz-transition:height 1s ease-out;transition:height 1s ease-out }

.active{  }
@media screen and (max-width: 767px) {
	.process-gem {
		width: 80% !important;
	}
}
	@media screen and (max-width: 767px) {
	.process-flow {
		margin-left: 0 !important;
		width: 100% !important;
	}
	.process-flow>li {
		width: 100% !important;
	}
	.process-flow>li h4 {
		top: 0 !important;
		text-align: center;
		display: block;
		width: 100% !important;
		right: 0 !important;
		padding: 0 !important;
	}
	.process-gem {
		background: none !important;
		display: block !important;
		box-shadow: none !important;
		text-align: center;
		width: 84%;
	}
	.process-flow ol.points {
		list-style: none !important;
		display: block !important;
		width: 100% !important;
		margin: 0 !important;
		text-align: center;
		margin-bottom: 12px !important;
		margin-top: 12px !important;
	}
	.process-flow .bar {
		text-align: center;
		margin: 0 auto;
	}
}
.border{
  border-right: 1px solid #fff;
  height: 316px;
}


.wrap
{
	margin: auto;
	max-width: 100%;
	zoom: 1;
	z-index: 15;
}
.wrap:before,.wrap:after
{
	content: "\0020";
	display: block;
	height: 0;
	overflow: hidden;
}
.wrap:after
{
	clear: both;
}

@media screen and (min-width:40em)
{
	.wrap
	{
		//padding: 1.6em 0;
	}
}


.animate
{
	position: relative;
}
.sub-header
{
	margin-bottom: 1.6em;
	position: relative;
}
@media screen and (min-width:40em)
{
	.sub-header
	{
		display: inline-block;
		padding: 0 5em;
		position: relative;
	}
	.sub-header:before,.sub-header:after
	{
		border-bottom: .1em solid rgba(255,255,255,0.5);
		content: "";
		display: inline-block;
		position: absolute;
		top: 50%;
		width: 4em;
	}
	.sub-header:before
	{
		left: 0;
	}
	.sub-header:after
	{
		right: 0;
	}
}
.mast-head
{
	background: #4c4c4e;
	height: 52px;
	left: 0;
	moz-transition: all .5s ease-out .4s;
	padding: .8em 1.6em;
	position: fixed;
	right: 0;
	transition: all .5s ease-out .4s;
	webkit-transition: all .5s ease-out .4s;
	z-index: 10;
	zoom: 1;
}
.mast-head:before,.mast-head:after
{
	content: "\0020";
	display: block;
	height: 0;
	overflow: hidden;
}
.mast-head:after
{
	clear: both;
}
.mast-head .wrap
{
	padding: 0;
}
.mast-head h1
{
	float: left;
	height: 49px;
	margin: 0;
	width: 110px;
	position: relative;
	top: 4px;
	height: 45px;
	background: url(../images/logo.png) no-repeat 3px 0px;
}
[role=navigation] .menu-button
{
	float: right;
	height: 20px;
	left: 0;
	top: 16px;
	moz-opacity: .8;
	moz-transition: opacity .2s ease-out;
	opacity: 1;
	position: relative;
	transition: opacity .2s ease-out;
	webkit-opacity: .8;
	webkit-transition: opacity .2s ease-out;
	width: 26px;
}
[role=navigation] .menu-button:focus
{
	outline: 0;
}
[role=navigation] .menu-button:hover
{
	moz-opacity: 1;
	opacity: 1;
	webkit-opacity: 1;
}
[role=navigation] .menu-button img
{
	moz-transition: all .2s ease-out;
	position: absolute;
	right: 0;
	top: 0;
	transition: all .2s ease-out;
	webkit-transition: all .2s ease-out;
}
[role=navigation] .menu-button .close
{
	moz-opacity: 0;
	opacity: 0;
	right: -1em;
	webkit-opacity: 0;
}
#nav
{
	color: #fff;
	float: right;
	font-family: "klavika_lightplain",sans-serif;
	moz-transition: all .2s ease-out;
	moz-transition: all .5s ease-out .4s;
	position: relative;
	transition: all .2s ease-out;
	transition: all .5s ease-out .4s;
	webkit-transition: all .2s ease-out;
	webkit-transition: all .5s ease-out .4s;
	zoom: 1;
}
#nav:before,#nav:after
{
	content: "\0020";
	display: block;
	height: 0;
	overflow: hidden;
}
#nav:after
{
	clear: both;
}
#nav a
{
	color: #fff;
	font-style: normal;
	font-size: 1em;
	text-decoration: none;
	text-transform: uppercase;
}

#nav a:hover{
	color: #18f7f9;
	text-decoration: none;

	-webkit-transition: color 0.6s ease-in-out;
	-moz-transition: color 0.6s ease-in-out;
	-ms-transition: color 0.6s ease-in-out;
	-o-transition: color 0.6s ease-in-out;
	transition: color 0.6s ease-in-out;
}
.open .mast-head
{
	background: #4c4c4e;
}
.open #nav
{
	moz-opacity: 1;
	opacity: 1;
	right: 0;
	webkit-opacity: 1;
}
.open .menu-button .close
{
	moz-opacity: 1;
	opacity: 1;
	right: 0;
	webkit-opacity: 1;
}
.open .menu-button .open
{
	moz-opacity: 0;
	opacity: 0;
	right: 1em;
	webkit-opacity: 0;
}
#nav
{
	background: #4c4c4e;
	bottom: 0;
	box-shadow: inset 0 0 1em 0 rgba(0,0,0,0.2);
	moz-box-shadow: inset 0 0 1em 0 rgba(0,0,0,0.2);
	position: fixed;
	right: -15em;
	top: -20px;
	webkit-box-shadow: inset 0 0 1em 0 rgba(0,0,0,0.2);
	width: 15em;
	z-index: 9999;
}
#nav li
{
	border-bottom: 1px solid rgba(255,255,255,0.1);
	padding: .8em;
	text-align: left;
	text-indent: .8em;
}
.open
{
	right: 15em;
}
.open .mast-head
{
	left: -15em;
	right: 15em;
}
@media screen and (min-width:40em)
{
	body
	{
		position: static;
	}
	body.open
	{
		right: 0;
	}
	.mast-head
	{
		left: 0;
		right: 0;
	}
	#nav
	{
		background: #18f7f9;
		box-shadow: none;
		moz-box-shadow: none;
		moz-opacity: 0;
		opacity: 0;
		position: relative;
		top: -4px;
		right: -30em;
		background: none;
		webkit-box-shadow: none;
		webkit-opacity: 0;
		width: 37em;
	}
	#nav li
	{
		border: 0 none;
		float: left;
		padding: 0 15px 0 0 !important;
		text-align: center;
		text-indent: 0;

	}
	.open .mast-head
	{
		left: 0;
		right: 0;
	}
}
@media screen and (min-width:45em)
{
	#nav
	{
		width:36em;
		right: -55em;

		position: relative;
		top: -2px;
		background: none;
}
}



